        <el-radio v-model="historyForm.normal" label="31-35"></el-radio>
      </el-form-item>
      <el-form-item label=" ">
        <el-radio v-model="historyForm.abnormal" label="1"></el-radio>
        <el-radio v-model="historyForm.abnormal" label="1-21"></el-radio>
        <el-radio v-model="historyForm.abnormal" label="2-31"></el-radio>
        <el-radio v-model="historyForm.abnormal" label="3-41"></el-radio>
        <el-radio v-model="historyForm.abnormal" label="4-61"></el-radio>
        <el-radio v-model="historyForm.abnormal" label="61"></el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.cyclicity_sum" label="2"></el-radio>
