        <el-radio v-model="historyForm.cyclicity_sum" label="7"></el-radio>
        <el-input v-model="historyForm.cyclicity_sum_qita"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_cond" label="5()"></el-radio>
        <el-radio v-model="historyForm.blood_cond" label="6-10()"></el-radio>
        <el-radio v-model="historyForm.blood_cond" label="11-19()"></el-radio>
        <el-radio v-model="historyForm.blood_cond" label="20()"></el-radio>
        <el-radio v-model="historyForm.blood_cond" label=","></el-radio>
        <el-input v-model="historyForm.blood_cond_qita"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-radio v-model="historyForm.blood_color" label=""></el-radio>
        <el-input v-model="historyForm.blood_color_qita"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_quality" label=""></el-radio>
        <el-radio v-model="historyForm.blood_quality" label=""></el-radio>
        <el-radio v-model="historyForm.blood_quality" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
        <el-radio v-model="historyForm.blood_block" label=""></el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_character" label=""></el-radio>
        <el-radio v-model="historyForm.blood_character" label=""></el-radio>
        <el-radio v-model="historyForm.blood_character" label=""></el-radio>
        <el-radio v-model="historyForm.blood_character" label=""></el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="historyForm.menstruation_is_accompany" active-text="" inactive-text=""></el-switch>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="historyForm.last_time"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-form-item label="">
          <el-radio v-model="historyForm.leucorrhea_quantity" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_quantity" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_quantity" label=""></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="historyForm.leucorrhea_color" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_color" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_color" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_color" label=""></el-radio>
          <el-input v-model="historyForm.leucorrhea_color_qita"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="historyForm.leucorrhea_feature" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_feature" label=""></el-radio>
          <el-radio v-model="historyForm.leucorrhea_feature" label=""></el-radio>
        </el-form-item>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
        <el-radio v-model="historyForm.marriage" label=""></el-radio>
      </el-form-item>

      <el-form-item label="">
          <el-input v-model="historyForm.pastpreg_yuncount"></el-input>
          <el-input v-model="historyForm.pastpreg_shunchan"></el-input>
          <el-input v-model="historyForm.pastpreg_pougong"></el-input>
          <el-input v-model="historyForm.pastpreg_zaochan"></el-input>
          <el-input v-model="historyForm.pastpreg_yaoliu"></el-input>
          <el-input v-model="historyForm.pastpreg_renliu"></el-input>
          <el-input v-model="historyForm.pastpreg_ziranliu"></el-input>
          <el-input v-model="historyForm.pastpreg_yiweirenshen"></el-input>
          <el-input v-model="historyForm.pastpreg_qinggongshu"></el-input>
          <el-input v-model="historyForm.pastpreg_qita"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="historyForm.prevent_wu" active-text="" inactive-text=""></el-switch>
        <el-checkbox v-model="historyForm.prevent_jieza"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.prevent_jieyuqi"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.prevent_biyuntao"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.prevent_biyunyao"  label="" ></el-checkbox>
      </el-form-item>

      <el-form-item label="">
        
        <el-switch v-model="historyForm.is_pastfamily_womb" active-text="" inactive-text=""></el-switch>
        <br>
        <el-checkbox v-model="historyForm.pastfamily_minus"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.pastfamily_plus"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.pastfamily_duonangluanchao"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.pastfamily_tangniaobing"  label="" ></el-checkbox>
        <el-checkbox v-model="historyForm.pastfamily_buxiang"  label="" ></el-checkbox>
        <el-input v-model="historyForm.pastfamily_qita"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" v-if="exist"  @click="updateHistoryForm"></el-button>
        <el-button type="primary" v-else  @click="createHistoryForm"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
      </el-form-item>

    </el-form>
  </el-dialog>
</template>
<script>
import { apiUpdatePatientHistoryForm, apiCreatePatientHistoryForm } from '@/api/api'
export default {
  name:'HistoryForm',
  data() {
    return {
      historyForm:{
        first_time:'', first_time_qita:'',            //
        normal:'', abnormal:'',                       //
        cyclicity_sum:'', cyclicity_sum_qita:'',      //
        blood_cond:'', blood_cond_qita:'',            //
        blood_color:'', blood_color_qita:'',          //
        blood_quality:'', blood_block:'',             //
        blood_character:'',                           //
        menstruation_is_accompany:'',                 //
        last_time:'',                                 //
        leucorrhea_quantity:'',                       //
        leucorrhea_color:'', leucorrhea_color_qita:'',//
        leucorrhea_feature:'',                        //
        marriage:'',                                  //
        //
        pastpreg_yuncount:'', pastpreg_shunchan:'', pastpreg_pougong:'', pastpreg_zaochan:'', pastpreg_yaoliu:'', pastpreg_renliu:'', pastpreg_ziranliu:'', pastpreg_yiweirenshen:'', pastpreg_qinggongshu:'', pastpreg_qita:'',
        //
        prevent_wu:'', prevent_jieza:'', prevent_jieyuqi:'', prevent_biyuntao:'', prevent_biyunyao:'',
        //
        is_pastfamily_womb:'', pastfamily_minus:'', pastfamily_plus:'', pastfamily_duonangluanchao:'', pastfamily_tangniaobing:'', pastfamily_buxiang:'', pastfamily_qita:''
      },
      dialogVisible: false,
      exist: true,
    }
  },
  methods: {
    updateHistoryForm () {
      apiUpdatePatientHistoryForm(this.historyForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    createHistoryForm () {
      apiCreatePatientHistoryForm(this.historyForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    resetDialog () {
      this.historyForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      this.dialogVisible = true
      this.exist = data.exist
      if (!data.exist) {
        //
        this.historyForm.info = data.historyForm.info
      } else {
        //()
        this.historyForm = data.historyForm
      }
    })
  }

};
</script>
<style lang="">
</style>


