          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="bxrayForm.zigong_kuan">
                <template slot="append">mm</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="bxrayForm.zigong_qianhou">
                <template slot="append">mm</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="">
            <el-switch v-model="bxrayForm.zigong_xingtai" active-text="" inactive-text=""></el-switch>
            <el-form-item label="">
            <el-input v-model="bxrayForm.zigong_juti"></el-input>
            </el-form-item>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="bxrayForm.zigong_neimo"></el-input>
        </el-form-item>
        <el-form-item label="">
