              <el-col :span="8">
                <el-form-item label="">
                  <el-input v-model="bxrayForm.zigong_daxiaochang">
                    <!-- <template slot="append">mm</template> -->
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="">
                  <el-input v-model="bxrayForm.zigong_daxiaokuan">
                    <!-- <template slot="append">mm</template> -->
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="">
                  <el-input v-model="bxrayForm.zigong_daxiaogao">
                    <!-- <template slot="append">mm</template> -->
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="bxrayForm.zigong_xainji" active-text="" inactive-text=""></el-switch>
        </el-form-item>
