          <el-form-item label="">
            <el-input v-model="bxrayForm.zuo_daxiaochang"></el-input>
            <el-input v-model="bxrayForm.zuo_daxiaokuan"></el-input>
          </el-form-item>
        </el-form-item>

        <el-form-item label="">
          <el-radio v-model="bxrayForm.zuo_xingtai" label=""></el-radio>
          <el-radio v-model="bxrayForm.zuo_xingtai" label="PCO 12">PCO 12</el-radio>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="bxrayForm.zuo_paoshu"></el-input>
        </el-form-item>

      </el-form-item>

      <el-form-item label="">

        <el-form-item label="">
          <el-input v-model="bxrayForm.you_chang"></el-input>
          <el-input v-model="bxrayForm.you_kuan"></el-input>
          <el-input v-model="bxrayForm.you_qianhou"></el-input>
          <el-input v-model="bxrayForm.you_tiji"></el-input>
        </el-form-item>

        <el-form-item label=">10mm">
          <el-switch v-model="bxrayForm.you_nangzhong" active-text="" inactive-text=""></el-switch>
          <el-form-item label="">
            <el-input v-model="bxrayForm.you_daxiaochang"></el-input>
            <el-input v-model="bxrayForm.you_daxiaokuan"></el-input>
          </el-form-item>
        </el-form-item>

        <el-form-item label="">
