          <el-radio v-model="bxrayForm.you_xingtai" label="PCO 12">PCO 12</el-radio>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="bxrayForm.you_paoshu"></el-input>
        </el-form-item>

      </el-form-item>

      <el-form-item>
        <el-button type="primary" v-if="exist"  @click="updateBxrayForm"></el-button>
        <el-button type="primary" v-else  @click="createBxrayForm"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
      </el-form-item>

    </el-form>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientBxrayForm, apiCreatePatientBxrayForm } from '@/api/api'
export default {
  name:'BxrayForm',
  data() {
    return {
      bxrayForm:{
        zigong_chang: '', zigong_kuan: '', zigong_qianhou: '',
        zigong_xingtai: '', zigong_juti: '',
        zigong_neimo: '',
        zigong_is_jiliu: '', zigong_weizhi: '', zigong_geshu: '', zigong_daxiaochang: '', zigong_daxiaokuan: '', zigong_daxiaogao: '',
        zigong_xainji: '',
        zuo_chang: '', zuo_kuan: '', zuo_qianhou: '', zuo_tiji: '',
        zuo_nangzhong: '', zuo_daxiaochang: '', zuo_daxiaokuan: '',
        zuo_xingtai: '',
        zuo_paoshu: '',
        you_chang: '', you_kuan: '', you_qianhou: '', you_tiji: '',
        you_nangzhong: '', you_daxiaochang: '', you_daxiaokuan: '',
        you_xingtai: '',
        you_paoshu: '',
        },
      mydata: {
        'zigong_chang':'',
        'zigong_kuan':'',
        'zigong_qianhou':'',
        'zigong_xingtai':'',
        'zigong_juti':'',
        'zigong_neimo':'',
        'zigong_is_jiliu':'',
        'zigong_weizhi':'',
        'zigong_geshu':'',
        'zigong_daxiaochang':'-',
        'zigong_daxiaokuan':'-',
        'zigong_daxiaogao':'-',
        'zigong_xainji':'',
        'zuo_chang':'',
        'zuo_kuan':'',
        'zuo_qianhou':'',
        'zuo_tiji':'',
        'zuo_nangzhong':'>10mm',
        'zuo_daxiaochang':'-',
        'zuo_daxiaokuan':'-',
        'zuo_xingtai':'',
        'zuo_paoshu':'',
        'you_chang':'',
        'you_kuan':'',
        'you_qianhou':'',
        'you_tiji':'',
        'you_nangzhong':'>10mm',
        'you_daxiaochang':'-',
        'you_daxiaokuan':'-',
        'you_xingtai':'',
        'you_paoshu':'',
      },
      dialogVisible: false,
      exist: true,
    }
  },
  methods: {
    updateBxrayForm () {
      apiUpdatePatientBxrayForm(this.bxrayForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    createBxrayForm () {
      apiCreatePatientBxrayForm(this.bxrayForm)
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    resetDialog () {
      this.bxrayForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      console.log('B',data)
      this.dialogVisible = true
      this.exist = data.exist
      if (!data.exist) {
        //
        this.bxrayForm.info = data.bxrayForm.info
      } else {
        //()
        this.bxrayForm = data.bxrayForm
      }

    })
  }

}
</script>
<style lang="">
</style>


